body
{
    margin: 0;
	padding: 0;
	background-color: #ffffff;
}

p{color: white;}

.title
{
    font-size: 2em; 
    p{color: white;}
}

.top-page
{
    height: 100%;  
    width: 100%; 
    position: relative;
    background-color: #2fff05;
    top: 0;
    left: 0;
    padding: 60px;
    z-index: -1;
    transform:rotate(-1deg);
}

.strip
{
    position: absolute;
    height: 75%;
    width: 100%;
    top: 0;
    left: 0;
    background-color: #000000;
    padding: 10px;
    transform:rotate(1deg);
}
  
  /* ================================================================================================ */

  .close {
    position: absolute;
    right: 32px;
    top: 32px;
    width: 32px;
    height: 32px;
    opacity: 0.5;
  }
  .close:hover {
    opacity: 1;
  }
  .close:before, .close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: rgb(137, 255, 27);
  }
  .close:before {
    transform: rotate(45deg);
  }
  .close:after {
    transform: rotate(-45deg);
  }  